/*top naviagtion bar*/
.App-header {
  text-align: right;
  font-size: 12px;
  color: white;
  padding: 10px;
}

/*top gmail and images*/
.App-link {
  padding: 10px;
  text-decoration: none;
  color: black;
}

/*google logo*/
.glogo  {
  text-align: center;
  display: block;
  margin: 0 auto;
  clear: both;
  padding-top: 130px;
  padding-bottom: 20px;
  justify-content: center;
}

/*search bar*/
.search {
  background-position: 10px 10px; 
  background-repeat: no-repeat;
  text-align: center;
  display: block;
  margin: 0 auto;
  clear: both;
  padding-top: 18px;
  padding-bottom: 20px;
  justify-content: center;
}

input[type=text] {
  width: 100%;
  outline: none;
  box-sizing: border-box;
  border: none;
  border-radius: 20px;
  font-size: 16px;
  background-color: rgb(255, 255, 255);
  background-image: url('./images/search.png' );
  background-position: 10px 9px; 
  background-repeat: no-repeat;
  padding: 15px 10px 12px 40px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 2px 4px
}

input[type=text]:focus {
  outline: none;
}